/* eslint-env browser */

(function() {
  'use strict';

  var $topSlider = $('.js-top-slider');
  $topSlider.carousel({
    interval: 5000,
  });

  $('.js-top-slider').hammer().on('swiperight',function() {
    $topSlider.carousel('prev');
  });
  $('.js-top-slider').hammer().on('swipeleft',function() {
    $topSlider.carousel('next');
  });

  var isMobileMenu = false;

  checkInitMobileMenu();
  $(window).on('resize', checkInitMobileMenu);
  $('.js-mobile-menu-toggle').on('click', toggleMobileMenu);
  $('.js-mobile-menu').on('click', '.header-menu__icon', function() {
    $(this).parent().parent().toggleClass('show-menu');
  });

  function checkInitMobileMenu() {
    var windowWidth = $(window).width();

    if (windowWidth < 992) {
      if (!isMobileMenu) {
        createMobileMenu();
      }
    } else {
      removeMobileMenu();
    }
  }

  function createMobileMenu() {
    var $mobileMenu = $('.js-mobile-menu');
    var $headerMenu = $('.js-header-menu');

    $mobileMenu.html($headerMenu.clone());
    isMobileMenu = true;
  }

  function removeMobileMenu() {
    var $mobileMenu = $('.js-mobile-menu');
    $mobileMenu.html('');
    $('body').removeClass('menu-open');
    isMobileMenu = false;
  }

  function toggleMobileMenu() {
    $('body').toggleClass('menu-open');
  }

})();

